.c-comment {
  padding-bottom: 50px;
  position: relative;

  &__circle {
    background-color: $c-albescent;
    align-items: flex-end;
    position: absolute;
    border-radius: 50%;
    display: flex;
    height: 45px;
    width: 45px;
    z-index: -1;

    &--1,
    &--3,
    &--8 {
      height: 60px;
      width: 60px;
    }

    &--4,
    &--6 {
      height: 70px;
      width: 70px;
    }

    &--1 {
      left: 35%;
      top: 0;
    }

    &--2 {
      right: 29.5%;
      top: 7%;
    }

    &--3 {
      right: 18.5%;
      top: 15.5%;

      @include media ('<1050px') {
        right: 5%;
        top: 9%;
      }
    }

    &--4 {
      left: 18.5%;
      top: 29%;

      @include media ('<1100px') {
        left: 4%;
        top: 7%;
      }
    }

    &--5 {
      left: 33%;
      top: 44%;

      @include media ('<1050px') {
        left: 12%;
        top: 85%;
      }
    }

    &--6 {
      right: 34.5%;
      top: 44.5%;

      @include media ('<1300px') {
        right: 15%;
        top: 80%;
      }
    }

    &--7 {
      right: 25.5%;
      top: 61%;

      @include media ('<phone') {
        right: 7%;
          top: 92%;
      }
    }

    &--8 {
      left: 38.5%;
      top: 67%;

      @include media ('<desktop') {
        left: 25%;
        top: 68%;
      }
    }

  }

  &__wrapper {
    text-align: center;
    max-width: 490px;
    margin: 0 auto;
  }

  &__quote {
    font-family: $f-asul-regular;
    letter-spacing: $ls-mail;
    line-height: $lh-quote;
    display: inline-block;
    font-size: $fs-quote;
    margin: 135px 0 40px;
    color: $c-daisy;

    @include media ('<phone') {
      margin: 135px 0 25px;
    }
  }

  &__author {
    margin-bottom: 65px;
    line-height: 24px;

    @include media ('<phone') {
      margin-bottom: 25px;
    }

    &--bold {
      font-family: $f-chivo-bold;
      letter-spacing: $ls-sm;
    }

    &--gray {
      letter-spacing: $ls-sublogo;
      color: $c-silver;
    }
  }

  &__img {
    max-width: 107px;
    margin: 0 auto;
  }
}