@mixin backgroundContainCenter {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

@mixin backgroundCoverCenter {
  background-size: cover;
  background-position: center center;
}

@mixin containerGlobal {
  padding: 60px 30px 30px;
  margin: 0 auto;
  width: 100%;
}

@function em($px) {
  @return $px / $fs-base + em;
}

@function px($px) {
  @return $px + px;
}

@mixin size($px) {
  width: $px + px;
  height: $px + px;
}

@mixin maxCentered($widthPx) {
  max-width: $widthPx + px;
  margin: 0 auto;
}

@mixin fixedGlobal {
  justify-content: center;
  align-items: center;
  position: fixed;
  display: flex;
  z-index: 999;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}

@mixin absolutely {
  position: absolute;
  left: 0;
  top: 0;
}

@mixin absolutelyCenter {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

@mixin absolutelyCenterHT($top) {
  position: absolute;
  left: 0;
  right: 0;
  top: $top;
  margin: 0 auto;
}

@mixin absolutelyCenterHB {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
}

@mixin absolutelyFull {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

@mixin suptitle {
  letter-spacing: $ls-sublogo;
  font-family: $f-chivo-bold;
  line-height: $lh-address;
  font-size: $fs-sm;
  color: $c-daisy;
}
